import { 
    Sigma,
    LoadGEXF,
    LoadJSON,
    Filter
} from 'react-sigma';
import React, { Component } from 'react';

class NarratorGraph extends Component {
    constructor(props) {
        super(props)
        this.state = {selectedNode: null}
    }

    _nodeFilter(node) {
        // console.log(node)
        return node.size > 100
    }

    _edgeFilter(edge) {
        console.log(edge)
    }

    render() {
        return (
                <Sigma render="webgl" style={{width:"1000px", height:"800px"}}
                    settings={{defaultEdgeType: "arrow"}}
                    onClickNode={ e => this.setState({selectedNode: e.data.node.id}) } 
                    onClickStage={ e => this.setState({selectedNode: null})} >
                     <LoadJSON path={window.location.href + "/data/graph.json"}>
                        <Filter neighborsOf={ this.state.selectedNode } 
                        nodesBy={this._nodeFilter}
                        />
                   
                    </LoadJSON> 
                </Sigma>
        );
    }
}

export default NarratorGraph;


